* {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.3rem;
}

body{
    padding: 60px;
}

input {
    color: green;
    font-size: 1.2rem;
    width: 100%;
}

.card-grid{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.card{
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.card p{
    align-content: center;
    padding: 5px;
} 
.card img {
    max-height: 200px;
}